.coursesCard {
  padding: 50px 0;
}

.coursesCard .items {
  background-color: #fff;
  padding: 30px;
  text-align: center;
}

.coursesCard .img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #f3b600;
  padding: 15px;
}

.coursesCard img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.coursesCard .text {
  margin-left: 15px;
  text-align: left;
}

.coursesCard .text h1 {
  font-size: 30px;
  font-weight: 500;
  line-height: 45px;
}

.coursesCard .rate {
  margin: 20px 0;
  color: #f3b600;
}

.coursesCard .rate i {
  font-size: 13px;
  margin-right: 5px;
}

.coursesCard .details .dimg img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 20px;
}

.coursesCard .details .box {
  display: flex;
  align-items: center;
  color: grey;
}

.coursesCard .details span {
  color: #f3b600;
  font-weight: 500;
  font-size: 14px;
}

.coursesCard .price {
  margin: 30px 0;
  background-color: #f8f8f8;
  padding: 10px;
}

.coursesCard h3 {
  font-weight: 500;
  color: #f3b600;
}

/*-------------online---------*/
.online {
  text-align: center;
}

.online .box {
  box-shadow: 0 5px 25px -2px rgb(0 0 0 / 6%);
  background-color: #fff;
  padding: 30px 20px;
  transition: 0.5s;
}

.online .img {
  width: 80px;
  height: 80px;
  margin: auto;
  position: relative;
}

.online img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.online h1 {
  font-weight: 500;
  font-size: 20px;
  margin: 20px 0;
  line-height: 30px;
}

.online span {
  background-color: #f8f8f8;
  padding: 5px 20px;
  font-weight: 500;
  font-size: 15px;
  color: #f3b600;
  border-radius: 5px;
}

.online .box .show {
  opacity: 0;
}

.online .box:hover {
  background-color: #f3b600;
  border-radius: 5px;
  cursor: pointer;
}

.online .box:hover .show {
  opacity: 1;
  position: absolute;
  top: 0;
  left: 0;
}

.online .box:hover h1 {
  color: #fff;
}

.viewButton {
  color: 'inherit';
  width: "100px";
}

.pt50px {
  padding-top: 50px;
}

.BenefitImg {
  height: 200px !important;
  width: 200px !important;
}

.d-flex {
  display: flex;
}

.f-wrap {
  flex-wrap: wrap;
}

.m50 {
  margin: 50px;
}

.f-center {
  justify-content: center;
}

.mt10 {
  margin-top: 10px;
}

.mt20 {
  margin-top: 20px;
}

.ml30 {
  margin-left: 30px;
}

.ls {
  list-style-type: disc;
}

.color-red {
  color: red !important;
}

.w50 {
  width: 50%;
}

.grey-color {
  color: grey;
}

.bg-image {
  height: auto !important;
  width: 600px !important;
  display: block;
  margin: auto;
}

/*-------------online---------*/
@media screen and (max-width: 1200px) {
  .coursesCard .text h1 {
    font-size: 21px;
  }
}

@media screen and (max-width: 600px) {
  .coursesCard img {
    height: auto;
  }

  .coursesCard .text h1 {
    font-size: 21px;
    line-height: 25px;
  }

  .ml30 {
    margin-left: 15px;
  }
}