/*----------awrapper------------*/
.awrapper {
  /* background-image: url("../../../public/images/awrapper.webp"); */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 30vh;
  color: #fff;
}
.awrapper .box {
  padding: 70px 0;
}
.awrapper img {
  margin-right: 30px;
}
.awrapper h1 {
  font-size: 50px;
}
.awrapper h3 {
  font-size: 20px;
  font-weight: 500;
}
.pt70 { 
  padding-top: 70px !important;
}
.px100{
  padding-left: 100px;
  padding-right: 100px;
}
.grey-color{
  color: #999999;;
}
.text-center{
  text-align: center;
}
.mt20{
  margin-top: 20px;
}
.mt10{
  margin-top: 10px;
}
.list-style-disc{
list-style: disc;
}
.theme-text{
  color: #f3b600;
}
/*----------awrapper------------*/

@media screen and (max-width: 1200px) {
  .aboutHome .item h2 {
    font-size:  17px;
    margin-bottom: 15px;
  }
  .aboutHome .item {
    background-color: #fff;
    margin-top: 30px;
    padding: 10px;
    transition: 0.5s;
  }
}
@media screen and (max-width: 600px) {
  .aboutHome img {
    width: 50px;
    height: 50px;
  }
  .aboutHome .right {
    padding: 80px 10px;
  }
  .aboutHome .container {
    flex-direction: column-reverse;
  }
  .aboutHome .row {
    width: 100%;
  }
  .awrapper {
    height: 50vh;
  }
  .f15{
    font-size: 15px;
  }
}
